"use client";

import { useMemo, useState } from "react";

import { Badge } from "@/components/ui/badge";
import { Card } from "@/components/ui/card";
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import type { Project } from "@/lib/db/types";
import Link from "next/link";

type FilterState = {
  status: string;
  type: string;
};

const statusOptions = ["all", "active", "on_hold", "completed", "archived"] as const;
const typeOptions = ["all", "project", "relationship", "area"] as const;

export const ProjectsTable = ({ projects }: { projects: Project[] }) => {
  const [filters, setFilters] = useState<FilterState>({
    status: "all",
    type: "all",
  });

  const filteredProjects = useMemo(() => {
    return projects.filter((project) => {
      const statusMatches = filters.status === "all" || project.status === filters.status;
      const typeMatches = filters.type === "all" || project.type === filters.type;
      return statusMatches && typeMatches;
    });
  }, [filters, projects]);

  return (
    <Card className="p-4">
      <div className="flex flex-col gap-4 sm:flex-row sm:items-center sm:justify-between">
        <div className="flex gap-3">
          <Select
            value={filters.status}
            onValueChange={(value) => setFilters((prev) => ({ ...prev, status: value }))}
          >
            <SelectTrigger className="w-[180px]">
              <SelectValue placeholder="Status" />
            </SelectTrigger>
            <SelectContent>
              {statusOptions.map((option) => (
                <SelectItem value={option} key={option}>
                  {option === "all" ? "All statuses" : option.replace("_", " ")}
                </SelectItem>
              ))}
            </SelectContent>
          </Select>

          <Select
            value={filters.type}
            onValueChange={(value) => setFilters((prev) => ({ ...prev, type: value }))}
          >
            <SelectTrigger className="w-[180px]">
              <SelectValue placeholder="Type" />
            </SelectTrigger>
            <SelectContent>
              {typeOptions.map((option) => (
                <SelectItem value={option} key={option}>
                  {option === "all" ? "All types" : option}
                </SelectItem>
              ))}
            </SelectContent>
          </Select>
        </div>
      </div>

      <Table className="mt-6">
        <TableHeader>
          <TableRow>
            <TableHead>Name</TableHead>
            <TableHead>Status</TableHead>
            <TableHead>Type</TableHead>
            <TableHead>Priority</TableHead>
            <TableHead>Last activity</TableHead>
          </TableRow>
        </TableHeader>
        <TableBody>
          {filteredProjects.map((project) => (
            <TableRow key={project.id}>
              <TableCell className="font-medium">
                <Link href={`/projects/${project.id}`} className="hover:underline">
                  {project.name}
                </Link>
              </TableCell>
              <TableCell>
                <Badge variant="outline">{project.status}</Badge>
              </TableCell>
              <TableCell>{project.type}</TableCell>
              <TableCell>{project.priority}</TableCell>
              <TableCell>
                {project.lastActiveAt
                  ? new Date(project.lastActiveAt).toLocaleDateString()
                  : "â€”"}
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>

      {filteredProjects.length === 0 ? (
        <p className="mt-4 text-sm text-muted-foreground">
          No projects match the current filters.
        </p>
      ) : null}
    </Card>
  );
};

